	module.exports = {"data":{"wordpressPost":{"title":"BMJ Open to publish abstracts for the UCL Qualitative Health Research Network symposium","content":"<p>On the Tuesday <b>7<sup>th</sup> February 2017</b>, the UCL Qualitative Health Research Network will be hosting their third symposium entitled ‘<b><i><span lang=\"EN-US\">Engagement, Co-production, and Collaborative Meaning-Making: Collaboration in Qualitative Health Research’</span></i></b><span lang=\"EN-US\">, supported by <a href=\"https://wellcome.ac.uk/?gclid=CKGg9bSmgs8CFcIp0wodE2ILlA?gclid=CKGg9bSmgs8CFcIp0wodE2ILlA\">The Wellcome Trust</a></span><b><span lang=\"EN-US\">.</span></b></p>\n<p>Held within the <b>Institute of Child Health, 30 Guildford Street, London</b>, the Network invite all those with an interest in qualitative health research, from policy makers to the general public, to come along and engage in presentations, workshops and discussions on the theme of collaboration.</p>\n<p>Abstracts can be submitted up until the closing date of <b>26<sup>th</sup> September 2016,</b> with successful submissions published in <em>BMJ Open</em> shortly after the symposium. The call for abstracts and details on of the submission process are available <a href=\"http://www.ucl.ac.uk/qualitative-health-research-network/symposia\">here</a>.</p>\n<p>Registration will open in November 2016 but please check the <a href=\"http://www.ucl.ac.uk/qhrn\">website</a><span lang=\"EN-US\"> </span>or follow the <a href=\"https://twitter.com/ucl_qhrn\">QHRN on Twitter </a>to keep up to date on key dates and plans.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"596da3d8-3707-53e3-99f7-94d5792b315d"}}

